<template>
  <ion-page>
    <default-toolbar></default-toolbar>
    
    <ion-content :fullscreen="true">
      <ion-header collapse="condense">
        <ion-toolbar>
          <ion-title size="large">Cheer Editor</ion-title>
        </ion-toolbar>
      </ion-header>
      <ion-grid>
        <ion-row>
          <ion-col>
            <div id="container">
              <h1>Cheer Editor</h1>
              <div>id:{{state.cheers.id}}</div>
                <ion-list>
                  <ion-list-header style="text-align:left;">
                    <ion-input v-model="state.cheers.Title"></ion-input>
                  </ion-list-header>
                  <ion-reorder-group disabled="false" @ionItemReorder="doReorder($event)">
                    <chat-box
                      v-for="(cheer, i) in state.cheers.Cheer" :key="i"
                      :cheer="cheer"
                      @click="EditChatBox(state.cheers, i)"
                    ></chat-box>
                  </ion-reorder-group>
                  <ion-item>
                    <ion-avatar slot="start"><img src="https://pbs.twimg.com/profile_images/1384742457477238784/R_eEQtFX_400x400.jpg"></ion-avatar>
                    <ion-label text-wrap>
                      <p></p>
                    </ion-label>
                  </ion-item>
                  <ion-item>
                    <ion-avatar slot="start"><img src="https://pbs.twimg.com/profile_images/1384742457477238784/R_eEQtFX_400x400.jpg"></ion-avatar>
                    <ion-label text-wrap>
                      <p>Twitter</p>
                    </ion-label>
                  </ion-item>
                </ion-list>

              <ion-button @click="openActionSheet()">Add+</ion-button>
              <ion-button @click="UpdateCheerDoc(state.cheers)">Save</ion-button>
            </div>
            <!-- TODO: Save -->
          </ion-col>
          <!--
          <ion-col>
            <div>{{state.cheers}}</div>
          </ion-col>
          -->
        </ion-row>
      </ion-grid>
    </ion-content>
  </ion-page>

</template>


<script lang="ts">
import { IonContent, IonPage, IonTitle, modalController, IonInput, actionSheetController, IonReorderGroup} from '@ionic/vue';
import DefaultToolbar from '../../components/DefaultToolbar.vue';
import ChatBox from "../../components/cheer/ChatBox.vue";
import { onBeforeMount, onMounted, reactive } from 'vue';
import { useRouter } from "vue-router";
import { firestoreSimple, CheerRef } from "../../firebase/firebase";
import { Cheer, Cheers } from '../../types/Cheer';
import Editmodal from "./EditModal.vue";

export default {
  name: 'Editor',
  components: {
    IonContent,
    IonPage,
    IonTitle,
    DefaultToolbar,
    ChatBox,
    IonInput,
    IonReorderGroup,
  },
  setup(){
    const router = useRouter();
    const state = reactive({
      cheers: new Cheers(),
    })
    
    onBeforeMount(async ()=>{
      if (router.currentRoute.value.params.itemid == null){
        const cheer = firestoreSimple.collection<Cheers>({
          path: "/cheer"
        })
        const doc = await cheer.fetch("BYmbrhEq2z2MANHrZboQ")
        state.cheers = doc ?? new Cheers();
      }else{
        console.log("router-params", router.currentRoute.value.params);        
        const cheer = firestoreSimple.collection<Cheers>({
          path: "/cheer"
        })
        const doc = await cheer.fetch(router.currentRoute.value.params.itemid.toString())
        console.log(doc)
        state.cheers = doc ?? new Cheers();
      }
    })
    const AddChatbox = (cheers: Cheers = state.cheers)=>{
      cheers.Cheer.push(Object.assign({}, new Cheer())); //MEMO: Cheer{}Push
    }
    const AddChobitLink = (cheers: Cheers = state.cheers)=>{
      cheers.Cheer.push(Object.assign({}, new Cheer("chobit")))
    }
    const openActionSheet = async ()=>{
      const actionSheet = await actionSheetController.create({
        header: "Account Setting",
        cssClass: "my-custom-class",
        buttons: [
          {
            text: "Add textbox",
            handler: AddChatbox
          },
          {
            text: "Add chobit link",
            handler: AddChobitLink,
          },
          {
            text: "Close",
            role: "cancel"
          }
        ]
      });
      return actionSheet.present();
    }

    const doReorder = (event: CustomEvent)=>{
      console.log('Dragged from index', event.detail.from, 'to', event.detail.to);
      state.cheers.Cheer = event.detail.complete(state.cheers.Cheer);
      console.log('After complete', state.cheers.Cheer);
    }
    /*
    const EditChatBox = (CheerJson: Cheer, i: number)=>{
      CheerJson.texts = ["Clear"]
      console.log(state.cheers)
    }
    */
    return {
      state,
      openActionSheet,
      doReorder
      //EditChatBox
    }
  },
  methods:{
    async EditChatBox(cheers: Cheers, i: number){
      const modal = await modalController
        .create({
          component: Editmodal,
          cssClass: 'my-custom-class',
          componentProps: {
            title: 'Edit',
            cheers: cheers,
            index: i,
          },
        })
      return modal.present();
    },
    UpdateCheerDoc(cheers: Cheers){
      
      console.log("before update value",cheers)
      firestoreSimple.collection<Cheers>({
        path: "/cheer",
      }).set(
        cheers
      ).then(()=>{
        console.log("Cheer doc was updated", cheers);
      })
    },

  },
  /*
  methods:{
    async EditChatBox(CheerJson: Cheer, i: number)
  },*/
}
</script>

<style scoped>
#container {
  text-align: center;
  max-width:600px;
  margin: 0 auto;
}

p {
  
}
#container strong {
  font-size: 20px;
  line-height: 26px;
}

#container p {
  font-size: 16px;
  line-height: 22px;
  color: #8c8c8c;
  margin: 0;
}

#container a {
  text-decoration: none;
}
</style>