<template>
  <ion-content>
    <ion-header>
      <ion-toolbar>
        <ion-title>{{ title }}</ion-title>
        <ion-buttons slot="end">
          <ion-button @click="dissmiss()" color="">close</ion-button>
        </ion-buttons>
      </ion-toolbar>
    </ion-header>
    <ion-content class="ion-padding">
      <ion-list>
        <ion-item>
          <ion-label position="stacked">Icon Image URL</ion-label>
          <ion-input v-model="content.imgsrc"></ion-input>     
        </ion-item>
        <ion-item>
          <ion-label position="stacked">Text</ion-label>
          <ion-textarea class="autogrow" v-model="texts"></ion-textarea>
        </ion-item>
      </ion-list>
      <ion-button @click="deleteCheer()">Delete</ion-button>
    </ion-content>
  </ion-content>
</template>

<script lang="ts">
import { Cheers } from '@/types/Cheer';
import { IonContent, IonHeader, IonTitle, IonToolbar, modalController, IonInput, IonTextarea, IonButtons /*IonPage*/} from '@ionic/vue';
import { defineComponent } from 'vue';

export default defineComponent({
  components: { IonContent, IonHeader, IonTitle, IonToolbar, IonInput, IonTextarea, IonButtons},
  name: 'Modal',
  props: {
    title: { type: String, default: 'Super Modal' },
    cheers: {
      type: Cheers,
      required: true
    },
    index: {
      type: Number,
      required: true
    },
  },
  data(props) {
    return {
      content: props.cheers.Cheer[props.index],
    }
  },
  computed:{
    texts:{
      get(): string{
        return this.content?.texts.join("\n") ?? ""
      },
      set(value: string){
        if (this.content != null ){
          this.content.texts = value.split("\n") ?? ['']
        }
      }
    }
  },
  methods:{
    dissmiss(){
      modalController.dismiss();
    },
    deleteCheer(){
      this.$props.cheers.Cheer.splice(this.$props.index, 1);
      this.dissmiss();
    }
  },
});
</script>

<style scoped>
.autogrow textarea {
  min-height: 66px;
}
</style>