<template>
  <template v-if="cheer.type == 'chobit'">
    <ion-item>
      <iframe style="margin: 0 auto;" width="560" height="347" frameborder="0" allowfullscreen="" src="https://chobit.cc/embed/9iblv/89x2uola?aid=iranica"></iframe>
      <ion-reorder slot="end"></ion-reorder>
    </ion-item>
  </template>
  <template v-else>
    <ion-item>
      <ion-avatar slot="start">
        <img :src="cheer.imgsrc" /></ion-avatar>
      <ion-label text-wrap>
        <p v-for="text in cheer.texts" :key="text">
          <span v-html="sanitize(autoLink(text))" ></span>
        </p>
      </ion-label>
      <ion-reorder slot="end"></ion-reorder>
    </ion-item>
  </template>
</template>

<script lang="ts">
import Vue from "vue"
import { IonLabel, IonItem, IonAvatar, IonReorder } from '@ionic/vue';
import {Cheer} from "../../types/Cheer";
import sanitizeHtml from "sanitize-html";

export default {
  name: "ChatBox",
  components:{
    IonLabel, IonItem, IonAvatar, IonReorder
  },
  props:{
    cheer: Object as Vue.PropType<Cheer>
  },
  methods:{
    autoLink(text: string) {
      return text.replace(/(https?:\/\/[^\s]*)/g, "<a href='$1'>$1</a>");
    },
    sanitize(text: string){
      return sanitizeHtml(text)
    }
  }
};
</script>

