
import { IonContent, IonPage, IonTitle, modalController, IonInput, actionSheetController, IonReorderGroup} from '@ionic/vue';
import DefaultToolbar from '../../components/DefaultToolbar.vue';
import ChatBox from "../../components/cheer/ChatBox.vue";
import { onBeforeMount, onMounted, reactive } from 'vue';
import { useRouter } from "vue-router";
import { firestoreSimple, CheerRef } from "../../firebase/firebase";
import { Cheer, Cheers } from '../../types/Cheer';
import Editmodal from "./EditModal.vue";

export default {
  name: 'Editor',
  components: {
    IonContent,
    IonPage,
    IonTitle,
    DefaultToolbar,
    ChatBox,
    IonInput,
    IonReorderGroup,
  },
  setup(){
    const router = useRouter();
    const state = reactive({
      cheers: new Cheers(),
    })
    
    onBeforeMount(async ()=>{
      if (router.currentRoute.value.params.itemid == null){
        const cheer = firestoreSimple.collection<Cheers>({
          path: "/cheer"
        })
        const doc = await cheer.fetch("BYmbrhEq2z2MANHrZboQ")
        state.cheers = doc ?? new Cheers();
      }else{
        console.log("router-params", router.currentRoute.value.params);        
        const cheer = firestoreSimple.collection<Cheers>({
          path: "/cheer"
        })
        const doc = await cheer.fetch(router.currentRoute.value.params.itemid.toString())
        console.log(doc)
        state.cheers = doc ?? new Cheers();
      }
    })
    const AddChatbox = (cheers: Cheers = state.cheers)=>{
      cheers.Cheer.push(Object.assign({}, new Cheer())); //MEMO: Cheer{}Push
    }
    const AddChobitLink = (cheers: Cheers = state.cheers)=>{
      cheers.Cheer.push(Object.assign({}, new Cheer("chobit")))
    }
    const openActionSheet = async ()=>{
      const actionSheet = await actionSheetController.create({
        header: "Account Setting",
        cssClass: "my-custom-class",
        buttons: [
          {
            text: "Add textbox",
            handler: AddChatbox
          },
          {
            text: "Add chobit link",
            handler: AddChobitLink,
          },
          {
            text: "Close",
            role: "cancel"
          }
        ]
      });
      return actionSheet.present();
    }

    const doReorder = (event: CustomEvent)=>{
      console.log('Dragged from index', event.detail.from, 'to', event.detail.to);
      state.cheers.Cheer = event.detail.complete(state.cheers.Cheer);
      console.log('After complete', state.cheers.Cheer);
    }
    /*
    const EditChatBox = (CheerJson: Cheer, i: number)=>{
      CheerJson.texts = ["Clear"]
      console.log(state.cheers)
    }
    */
    return {
      state,
      openActionSheet,
      doReorder
      //EditChatBox
    }
  },
  methods:{
    async EditChatBox(cheers: Cheers, i: number){
      const modal = await modalController
        .create({
          component: Editmodal,
          cssClass: 'my-custom-class',
          componentProps: {
            title: 'Edit',
            cheers: cheers,
            index: i,
          },
        })
      return modal.present();
    },
    UpdateCheerDoc(cheers: Cheers){
      
      console.log("before update value",cheers)
      firestoreSimple.collection<Cheers>({
        path: "/cheer",
      }).set(
        cheers
      ).then(()=>{
        console.log("Cheer doc was updated", cheers);
      })
    },

  },
  /*
  methods:{
    async EditChatBox(CheerJson: Cheer, i: number)
  },*/
}
