
import { Cheers } from '@/types/Cheer';
import { IonContent, IonHeader, IonTitle, IonToolbar, modalController, IonInput, IonTextarea, IonButtons /*IonPage*/} from '@ionic/vue';
import { defineComponent } from 'vue';

export default defineComponent({
  components: { IonContent, IonHeader, IonTitle, IonToolbar, IonInput, IonTextarea, IonButtons},
  name: 'Modal',
  props: {
    title: { type: String, default: 'Super Modal' },
    cheers: {
      type: Cheers,
      required: true
    },
    index: {
      type: Number,
      required: true
    },
  },
  data(props) {
    return {
      content: props.cheers.Cheer[props.index],
    }
  },
  computed:{
    texts:{
      get(): string{
        return this.content?.texts.join("\n") ?? ""
      },
      set(value: string){
        if (this.content != null ){
          this.content.texts = value.split("\n") ?? ['']
        }
      }
    }
  },
  methods:{
    dissmiss(){
      modalController.dismiss();
    },
    deleteCheer(){
      this.$props.cheers.Cheer.splice(this.$props.index, 1);
      this.dissmiss();
    }
  },
});
